body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
a {
  color: darkslategray;
}
a:hover {
  color: saddlebrown;
}
h1 {
  color: darkslategray;
}
h2 {
  color: saddlebrown;
}
h3 {
  text-transform: uppercase;
}
.hidden {
  position: absolute;
  left: -10000px;
}

.color {
  display: inline-block;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  padding-top: 2rem;
  margin: 0.3rem;
  cursor: pointer;
  -moz-user-select: none; 
  -webkit-user-select: none; 
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}
.item-container {
  padding: 1.5rem 2%;
  padding-bottom: 0.5rem;
}
.palette-collapse form {
  margin-left: auto;
  margin-right: auto;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.nav {
  display: inline;
  display: initial;
  flex-wrap: none;
}
.nav-tabs {
  border: none;
}
.nav-tabs > a {
  display: inline-block;
  border: none !important;
  color: darkslategray;
  font-size: 1.2rem;
  font-weight: bold;
}
.nav-tabs > a:hover {
  color: saddlebrown;
}
.nav-link.active {
  border: none;
}
