.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.nav {
  display: initial;
  flex-wrap: none;
}
.nav-tabs {
  border: none;
}
.nav-tabs > a {
  display: inline-block;
  border: none !important;
  color: darkslategray;
  font-size: 1.2rem;
  font-weight: bold;
}
.nav-tabs > a:hover {
  color: saddlebrown;
}
.nav-link.active {
  border: none;
}